/* App.css */

/* Default light theme styles */
body {
  background-color: white;
  color: black;
  transition: background-color 0.3s, color 0.3s;
}

/* Dark theme styles */
body.dark {
  background-color: #121212;
  color: white;
}




header, nav, main, footer {
  padding: 1em;
}

.container {
  display: grid;
  grid-template-areas:
    "header"
    "nav"
    "main"
    "footer";
  grid-gap: 1em;
}

header {
  grid-area: header;
  background: #f4f4f4;
}

nav {
  grid-area: nav;
  background: #e2e2e2;
}

main {
  grid-area: main;
  background: #fff;
}

footer {
  grid-area: footer;
  background: #ddd;
}

@media (min-width: 600px) {
  .container {
    grid-template-areas: 
      "header header"
      "nav main"
      "footer footer";
    grid-template-columns: 1fr 3fr;
  }
}
